import { Metadata } from "next";
import Script from "next/script";
import Layout from "@/components/Layout";
import NewsUpdates from "@/components/NewsUpdates";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";

const queryClient = new QueryClient();

// ------------------------------------------
// SEO METADATA (SSR)
// ------------------------------------------
export const metadata: Metadata = {
  title:
    "News & Updates - Legal Case Updates & Publications | Commercial Law Chamber",
  description:
    "Stay informed with the latest news, case updates, publications, and deal corner insights from Commercial Law Chamber. Updates on tax law, commercial disputes, and regulatory developments.",
  keywords:
    "legal news, case updates, legal publications, tax law news, commercial disputes updates, regulatory news, deal corner, legal updates India, law firm news",
  alternates: {
    canonical: "https://www.clclaw.in/news",
  },
  openGraph: {
    title:
      "News & Updates - Legal Case Updates & Publications | Commercial Law Chamber",
    description:
      "Latest news, case updates, and publications on tax law, commercial disputes, and regulatory developments.",
    url: "https://www.clclaw.in/news",
    type: "website",
    images: ["https://www.clclaw.in/clc-logo.png"],
    locale: "en_IN",
  },
  twitter: {
    card: "summary_large_image",
    title: "News & Updates - Commercial Law Chamber",
    description: "Latest legal news, case updates, and publications.",
    images: ["https://www.clclaw.in/clc-logo.png"],
  },
  robots:
    "index, follow, max-image-preview:large, max-snippet:-1, max-video-preview:-1",
  other: {
    // JSON-LD Schema (SSR)
    "script:ld+json": JSON.stringify({
      "@context": "https://schema.org",
      "@type": "CollectionPage",
      name: "News & Updates",
      description:
        "Latest news, case updates, publications, and deal corner updates from Commercial Law Chamber",
      url: "https://www.clclaw.in/news",
      mainEntity: {
        "@type": "ItemList",
        itemListElement: [
          {
            "@type": "ListItem",
            position: 1,
            item: {
              "@type": "NewsArticle",
              name: "Legal News & Updates",
              url: "https://www.clclaw.in/news",
              description:
                "Latest legal updates, case summaries, and regulatory developments.",
              publisher: {
                "@type": "Organization",
                name: "Commercial Law Chamber",
                url: "https://www.clclaw.in",
                logo: "https://www.clclaw.in/clc-logo.png",
              },
              about: [
                "Case Updates",
                "Legal News",
                "Deal Corner",
                "Tax Law",
                "Commercial Disputes",
                "Regulatory Developments",
              ],
            },
          },
        ],
      },
      breadcrumb: {
        "@type": "BreadcrumbList",
        itemListElement: [
          {
            "@type": "ListItem",
            position: 1,
            name: "Home",
            item: "https://www.clclaw.in",
          },
          {
            "@type": "ListItem",
            position: 2,
            name: "News & Updates",
            item: "https://www.clclaw.in/news",
          },
        ],
      },
    }),
  },
};

// ------------------------------------------
// PAGE (SERVER COMPONENT)
// ------------------------------------------
export default function NewsPage() {
  return (
    <>
      <QueryClientProvider client={queryClient}>
        <Layout>
          <NewsUpdates />
        </Layout>
      </QueryClientProvider>
    </>
  );
}
